.nav-desktop {
  display: none;
  width: 100%;

  @include forTablet {
    display: block;
    margin-bottom: 67px;
    top: 0;
    position: sticky;
    background-color: #f7f7f7;
    z-index: 3;

  }

  @include forDesktop {
    display: block;
  }

  &__menu {
    display: flex;
    margin: 0 auto;
    align-items: center;
    height: 76px;

    @include forTablet {
      width: 700px;
    }

    @include forDesktop {
      width: 785px;
    }
  }

  &__list {
    position: relative;
    display: flex;
    width: 100%;
    justify-content: space-between;
  }

  &__item {
    display: block;
  }

  &__link {
    display: block;
    text-transform: uppercase;
    text-decoration: none;
    font-weight: 400;
    font-size: 14px;
    line-height: 20px;
    text-align: center;
    border-bottom: 1px solid transparent;
    color: $black-crazy;
    transition: all 0.3s;

    &:hover {
      font-family: Inter, Arial, Helvetica, sans-serif;
      font-weight: 700;
      border-bottom: 1px solid $black-crazy;

    }
  }
}
