.menu {
  background-color: #f7f7f7;

  &__content {
    position: relative;
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-direction: column;
    box-sizing: border-box;
    height: 100vh;
    padding-top: 40px;

    @media (orientation: landscape) {
      padding-top: 20px;
    }
  }

  &__top {
    display: flex;
    padding-top: 25px;
    margin-bottom: 40px;
    justify-content: center;

    @media (orientation: landscape) {
      padding-top: 20px;
      margin-bottom: 10px;
    }
  }

  &__list {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    list-style: none;
    margin: 0 0 24px;
    padding: 0;
  }

  &__item {
    position: relative;
    margin-bottom: 40px;
    width: max-content;
    text-align: center;
    border-bottom: 1px solid transparent;

    &:hover {
      border-bottom: 1px solid $black-crazy;
    }

    @media (orientation: landscape) {
      margin-bottom: 10px;
    }

    &:last-of-type {
      margin-bottom: 100px;

      @media (orientation: landscape) {
        margin-bottom: 0;
      }
    }
  }

  &__link {
    display: inline-block;
    padding-bottom: 8px;
    font-size: 26px;
    line-height: 26px;
    flex-shrink: 0;
    text-decoration: none;
    font-family: Inter-bold, Arial, Helvetica, sans-serif;
    color: $black-crazy;
    text-transform: uppercase;
    transition: all 0.4s;
  }

  @include forTablet {
    display: none;
  }

  &__buy {
    position: sticky;
    bottom: 0;

    &:hover {
      background: #131313;
    }

    &:active {
      transform: scale(0.9);
    }
  }

  @include forTablet {
    display: none;
  }
}
