.icon {
  display: block;
  background-size: contain;
  background-repeat: no-repeat;
  background-position: center;
  transition: all 0.2s;

  &:hover {
    cursor: pointer;
  }

  &__menu-mobile {
    background-image: url(/images/menu.svg);

    width: 24px;
    height: 14px;

    &:hover {
      width: 27px;
      height: 14px;

      background-image: url(/images/menu_h.svg) ;
    }

    @include forTablet {
      display: none;
    }
  }

  &__menu-close {
    position: absolute;
    background-image: url(/images/close.png);
    width: 40px;
    height: 30px;
    background-position: center;
    background-size: contain;
    transition: transform 0.3s;
    right: 30px;

    &:hover {
      transform: scale(1.2);
    }
  }

  &__design {
    background-image: url(/images/benefits1.svg);
    background-position: center;
    background-size: cover;
    width: 50px;
    height: 40px;
    margin-bottom: 40px;
    transition: all 0.4s;

    &:hover {
      transform: scale(1.2);
    }

    @include forTablet {
      margin-bottom: 50px;
    }

    @include forDesktop {
      height: 42px;
      margin-bottom: 34px;
    }
  }

  &__speaker {
    background-image: url(/images/benefits2.svg);
    background-position: center;
    background-size: cover;
    width: 46px;
    height: 40px;
    transition: all 0.4s;

    &:hover {
      transform: scale(1.2);
    }

    @include forTablet {
      width: 46px;
      height: 40px;
      margin-bottom: 50px;
    }

    @include forDesktop {
      height: 43px;
      width: 49px;
      object-fit: contain;
      margin-bottom: 34px;
    }
  }

  &__multiroom {
    background-image: url(/images/benefits3.svg);
    background-position: center;
    background-size: cover;
    width: 46px;
    height: 37px;
    transition: all 0.4s;

    &:hover {
      transform: scale(1.2);
    }

    @include forTablet {
      height: 36px;
      margin-bottom: 44px;
    }

    @include forDesktop {
      height: 38px;
      margin-bottom: 38px;
    }
  }

  &__lighting {
    background-image: url(/images/benefits4.svg);
    background-position: center;
    background-size: cover;
    width: 44px;
    height: 43px;
    transition: all 0.4s;

    &:hover {
      transform: scale(1.2);
    }

    @include forTablet {

      height: 44px;
      margin-bottom: 37px;
    }

    @include forDesktop {

      height: 46px;
      margin-bottom: 30px;
    }
  }

  &__arrow-up {
    position: fixed;
    bottom: 150px;
    right: 20px;
    width: 20px;
    height: 20px;
    background-image: url(/images/arrow-up.svg);
    transition: all 0.4s;

    &:hover {
      height: 25px;
    }

    @include forTablet {
      right: 20px;
      bottom: 150px;
      transform: translateY(-30px);
    }

    @include forDesktop {
      right: 123px;
      bottom: 250px;
      transform: translateY(8px);
    }
  }

  &__facebook {
    background-image: url(/images/facebook.svg);
    width: 11px;
    height: 19px;
    border-bottom: 1px solid transparent;
    transition: all 0.4s;

    &:hover {
      transform: scale(1.5);
    }
  }

  &__twitter {
    background-image: url(/images/twitter.svg);
    width: 22px;
    height: 17px;
    transition: all 0.4s;

    &:hover {
      transform: scale(1.5);
    }
  }

  &__insta {
    background-image: url(/images/insta.svg);
    width: 18px;
    height: 18px;
    transition: all 0.4s;

    &:hover {
      transform: scale(1.5);
    }
  }
}
